/*eslint no-useless-escape: "off"*/

import { Component, OnDestroy, OnInit } from '@angular/core';
import { Observable, Subject, takeUntil } from 'rxjs';
import { TranslateService } from '@ngx-translate/core';
import { Store } from '@ngrx/store';
import { AbstractControl, AsyncValidatorFn, FormControl, FormGroup, ValidationErrors, Validators } from '@angular/forms';
import { map } from 'rxjs/operators';
import { MessageService } from 'primeng/api';

import Nickname from '@ApiClient/services/nickname';
import { Nickname as NicknameCommon } from '@ApiClient/backend/Common/nickname';
import { IUserData } from '@ApiClient/backend/User/user';
import { Register } from '@ApiClient/backend/Register/register';
import { LangCode } from '@ApiClient/backend/Common/language';

import { URLS } from '@enums/urls';
import { AppStateInterface } from '@store/app/app-state.interface';
import { selectEmail, selectIdentity, selectIsLoadingSettings } from '@store/settings/settings.selectors';
import { getUserSettingsAction, updateUserSettings } from '@store/settings/settings.actions';
import { selectCurrentUserNickname, selectCurrentUserPictureId, selectCurrentUserProfileName } from '@store/auth/auth.selectors';

interface IFormValues {
  nickname: string | null;
  profileName: string | null;
  firstName: string | null;
  lastName: string | null;
  birthDate: string | null;
  bio: string | null;
  website: string | null;
  image: string | null;
}

@Component({
  selector: 'app-settings',
  templateUrl: 'settings.component.html',
  styleUrls: ['settings.component.scss'],
})
export class SettingsComponent implements OnInit, OnDestroy {
  private unsubscribe$ = new Subject<void>();
  isLoading$: Observable<boolean> = this.store.select(selectIsLoadingSettings);
  initialFormValues: Partial<IFormValues> = {};

  maxDate!: Date;
  maxLengthName: number = Register.MaxNameLength;
  maxLengthLastName: number = Register.MaxLastNameLength;
  maxLengthNickname: number = NicknameCommon.MaxLengthNicknameUrl;
  maxLengthBio: number = 5000;
  maxLengthWebsite: number = 350;

  settingsForm = new FormGroup({
    email: new FormControl({ value: '', disabled: true }, [Validators.email]),
    nickname: new FormControl(
      '',
      [Validators.required, Validators.maxLength(this.maxLengthNickname), Validators.pattern(/^[a-zA-Z0-9_-]*$/)],
      [this.nicknameValidator()],
    ),
    profileName: new FormControl('', [Validators.required, Validators.maxLength(this.maxLengthName)]),
    firstName: new FormControl('', [Validators.required, Validators.maxLength(this.maxLengthLastName)]),
    lastName: new FormControl('', [Validators.required, Validators.maxLength(this.maxLengthLastName)]),
    birthDate: new FormControl('', [Validators.required, Validators.minLength(10)]),
    bio: new FormControl('', [Validators.maxLength(this.maxLengthBio)]),
    website: new FormControl('', [
      Validators.maxLength(this.maxLengthWebsite),
      Validators.pattern(/^(https?:\/\/)?([\da-z\.-]+)\.([a-z\.]{2,6})([\/\w \.-]*)*\/?$/),
    ]),
    image: new FormControl(''),
  });
  constructor(
    private store: Store<AppStateInterface>,
    private translate: TranslateService,
    private toast: MessageService,
  ) {}

  ngOnInit() {
    this.store.dispatch(getUserSettingsAction());
    this.maxDate = new Date();

    this.store
      .select(selectCurrentUserNickname)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe((nickname) => {
        this.settingsForm.patchValue({ nickname });
        this.initialFormValues.nickname = nickname ?? undefined;
      });

    this.store
      .select(selectCurrentUserPictureId)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe((image) => {
        this.settingsForm.patchValue({ image });
        this.initialFormValues.image = image ?? undefined;
      });

    this.store
      .select(selectCurrentUserProfileName)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe((profileName) => {
        this.settingsForm.patchValue({ profileName });
        this.initialFormValues.profileName = profileName ?? undefined;
      });

    this.store
      .select(selectIdentity)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe((identity) => {
        this.settingsForm.patchValue({ firstName: identity.firstName });
        this.settingsForm.patchValue({ lastName: identity.lastName });
        this.settingsForm.patchValue({ birthDate: this.formatDate(identity.birthDate as string) });
        this.settingsForm.patchValue({ bio: identity.bio });
        this.settingsForm.patchValue({ website: identity.website });

        this.initialFormValues.firstName = identity.firstName ?? undefined;
        this.initialFormValues.lastName = identity.lastName ?? undefined;
        this.initialFormValues.birthDate = this.formatDate(identity.birthDate as string) ?? undefined;
        this.initialFormValues.bio = identity.bio ?? undefined;
        this.initialFormValues.website = identity.website ?? undefined;
      });

    this.store
      .select(selectEmail)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe((email) => {
        this.settingsForm.patchValue({ email });
      });
  }
  ngOnDestroy() {
    this.unsubscribe$.next();
    this.unsubscribe$.complete();
  }

  onSubmit() {
    if (this.settingsForm.invalid) {
      this.settingsForm.markAllAsTouched();
      return;
    }

    const changedValues: Partial<IFormValues> = this.compareObjects<IFormValues>(this.settingsForm.value, this.initialFormValues);
    const isFormChanged = Object.values(changedValues).some((value) => value !== undefined);
    if (isFormChanged) {
      this.store.dispatch(
        updateUserSettings({
          settings: {
            nicknameDisplayed: changedValues.nickname,
            nicknameUrl: changedValues.nickname,
            name: changedValues.profileName,
            birthDate: changedValues.birthDate ? this.convertStringToDate(changedValues.birthDate) : undefined,
            firstName: changedValues.firstName,
            lastName: changedValues.lastName,
            biography: changedValues.bio,
            website: changedValues.website,
            langCode: this.translate.currentLang as keyof typeof LangCode,
          } as Partial<IUserData>,
        }),
      );
    } else {
      this.toast.add({
        severity: 'error',
        summary: this.translate.instant('ERROR.TITLE'),
        detail: this.translate.instant('ERROR.NOTHING_TO_CHANGE'),
      });
    }
  }

  private formatDate(value: string): string {
    const date = new Date(value);
    const day = date.getDate().toString().padStart(2, '0');
    const month = (date.getMonth() + 1).toString().padStart(2, '0');
    const year = date.getFullYear();
    return `${day}/${month}/${year}`;
  }

  private convertStringToDate(value: string): Date {
    const parts = value.split('/');
    const day = parseInt(parts[0], 10);
    const month = parseInt(parts[1], 10) - 1;
    const year = parseInt(parts[2], 10);
    return new Date(year, month, day);
  }

  private compareObjects<T>(obj1: Partial<T>, obj2: Partial<T>): Partial<T> {
    const res: Partial<T> = {};
    Object.keys(obj1).map((key) => {
      const typedKey = key as keyof Partial<T>;
      if (obj1[typedKey] === obj2[typedKey]) {
        res[typedKey] = undefined;
      } else {
        res[typedKey] = obj1[typedKey];
      }
    });
    return res;
  }

  private nicknameValidator(): AsyncValidatorFn {
    return (control: AbstractControl): Observable<ValidationErrors | null> | Promise<ValidationErrors | null> => {
      return Nickname.exist(control.value).pipe(
        map((isExist) => {
          if (!isExist || control.value === this.initialFormValues.nickname) {
            return null;
          } else {
            return { nicknameTaken: true };
          }
        }),
      );
    };
  }

  protected readonly URLS = URLS;
}
